"use client";

import { cn } from "@/lib/utils";
import {
  LayoutDashboard,
  Users,
  Briefcase,
  ClipboardList,
  AlertTriangle,
  FolderKanban,
  Menu,
} from "lucide-react";
import Link from "next/link";

interface Props {
  open: boolean;
  setOpen: (v: boolean) => void;
}

const navItems = [
  { title: "Dashboard", href: "/dashboard", icon: LayoutDashboard },
  { title: "Customers", href: "/customers", icon: Users },
  { title: "Providers", href: "/providers", icon: Briefcase },
  { title: "Categories", href: "/categories", icon: FolderKanban },
  { title: "Bookings", href: "/bookings", icon: ClipboardList },
  { title: "Disputes", href: "/disputes", icon: AlertTriangle },
];

export default function Sidebar({ open, setOpen }: Props) {
  return (
    <aside
      className={cn(
        "bg-[#0c1e4e] text-white h-screen sticky top-0 transition-all duration-300 z-40",
        open ? "w-60" : "w-20"
      )}
    >
      <div className="flex items-center justify-between px-4 py-5">
        <span className={cn("font-semibold text-lg", !open && "hidden")}>
          Workly
        </span>

        <button
          onClick={() => setOpen(!open)}
          className="p-2 hover:bg-white/10 rounded-lg"
        >
          <Menu size={20} />
        </button>
      </div>

      <nav className="mt-4 space-y-1 px-3">
        {navItems.map((item) => {
          const Icon = item.icon;

          return (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "flex items-center gap-3 px-3 py-2 rounded-lg text-sm hover:bg-white/10 transition",
                "text-white/90 hover:text-white"
              )}
            >
              <Icon size={20} />

              {open && <span>{item.title}</span>}
            </Link>
          );
        })}
      </nav>
    </aside>
  );
}
